<template>
<div class="xin">
    <Head :agood='1' :bgood='bgood'></Head>
      <div class="content">
          <div class="num">
             <div>
                 <input type="text" placeholder="请填写您的姓名" v-model="text1"/>
             </div>
             <div>
                 <input type="text" @click="btn" placeholder="小区/写字楼/学校等" v-model="text2" />
             </div>
             <div>
                 <input type="text" placeholder="请填写详细送餐地址" v-model="text3"/>
             </div>
             <div>
                 <input type="text" placeholder="请填写能够联系到您的手机号" v-model="text4"/>
             </div>
             <div>
                 <input type="text" placeholder="备用联系电话" v-model="text5"/>
             </div>
          </div>
          <div class="loginab">
          <input type="button" value="新增地址" @click='btn1'>
        </div>
      </div>
    
</div>
</template>

<script>
import axios from 'axios'
import Head from '@/components/head/Head'
export default {
    name:'xin',
    components:{Head},
    data(){
        return{
            bgood:'新增地址',
            text1:'',
            text2:'',
            text3:'',
            text4:'',
            text5:''
        }
    },
    mounted(){
        this.text2=this.$route.query.name
        // console.log(this.$route.query.name)
    },
    methods:{
        btn(){
            this.$router.push('/addres')
        },
        btn1(){
            axios.post('http://elm.cangdu.org/v1/users/3080/addresses',
                {
                    address: this.text3,
                    address_detail: this.text2,
                    geohash: this.$route.query.geohash,
                    name: this.text1,
                    phone: this.text4,
                    phone_bk: this.text5,
                    poi_type: 0,
                    sex: 1,
                    tag: "公司",
                    tag_type: 4
                }
            ).then((res)=>{
                this.$router.push('/goods')
            })
        }
    }

}
</script>

<style lang='scss'>
.content{
    width: 100%;
    margin-top: 1.16rem;
    .num{
        width: 100%;
        height: 5rem;
        background:white;
        div{
            width: 90%;
            height: 20%;
            line-height: 30%;
            margin: 0 auto;
            input{
                width: 100%;
                height: .6rem;
                font-size: .32rem;
                border: none;
                background: #f5f5f5;
                text-indent: 1em;
                border-radius: 0.1rem;
            }
        }
    }
}
.loginab{
  width: 95%;
  height: 1rem;
  margin: 0 auto;
  font-size: .2rem;
  input{
    width: 100%;
    height: 100%;
    background: #4cd964;
    border: none;
    border-radius: 0.05rem;
    font-size: .4rem;
    color: white;
  }
}
</style>


